import { memo, useMemo } from 'react'
import css from './Locations.module.scss'
import classnames from 'classnames'
import { PropsWithClassName } from 'specs/index.spec'
import IconMap from 'assets/icons/IconMap'
import { LanguageOptions } from 'utils/const'
import City, { CityNamePosition } from 'components/molecules/City/City'

function Locations ({
  className,
  lang
}: {
  lang: LanguageOptions
} & PropsWithClassName) {
  const citiesData = {
    [LanguageOptions.UA]: [
      {
        name: ``,
        position: CityNamePosition.LEFT,
        className: classnames(css.city, css.Palyanytsia)
      },
      {
        name: ``,
        position: CityNamePosition.LEFT,
        className: classnames(css.city, css.Uzhgorod)
      },
      {
        name: ``,
        className: classnames(css.city, css.Tyachiv)
      },
      {
        name: ``,
        className: classnames(css.city, css.Khust)
      },
    ],
    [LanguageOptions.ENG]: [
      {
        name: `Palyanytsia`,
        position: CityNamePosition.LEFT,
        className: classnames(css.city, css.Palyanytsia)
      },
      {
        name: `Uzhhorod`,
        position: CityNamePosition.LEFT,
        className: classnames(css.city, css.Uzhgorod)
      },
      {
        name: `Khust`,
        className: classnames(css.city, css.Khust)
      },
      {
        name: `Tyachiv`,
        className: classnames(css.city, css.Tyachiv)
      },
    ],
  }

  const cities = useMemo(() => {
    return citiesData[lang].map(({className, ...item}, index) => (
      <City
        className={className}
        key={index}
        {...item}
      />
    ))
  }, [citiesData, lang])

  return (
    <div className={className}>
      <IconMap className={css.map} />
        {cities}
    </div>
  )
}

export default memo(Locations)