import { memo, useContext, useMemo } from 'react'
import css from './Schedule.module.scss'
import Container from 'components/templates/Container/Container'
import Heading from 'components/atoms/Heading/Heading'
import { ScheduleData } from 'Pages/HomePage/Schedule/_assets/data'
import { LangContext } from 'components/organisms/LangProvider/LangProvider'

function Schedule () {
  const lang = useContext(LangContext)

  const {
    heading,
    list
  } = ScheduleData[lang]

  const items = useMemo(() => {
    return list.map(({duration, units, description}, index) => (
      <div className={css.item} key={index}>
        <dt className={css.duration}>
          <span className={css.counter}>
            {duration}
          </span>
          {units}
        </dt>
        <dd
          className={css.description}
          dangerouslySetInnerHTML={{__html: description}}
        />
      </div>
    ))
  }, [list])

  return (
    <section className={css.wrapper}>
      <Container>
        <Heading
          dangerouslySetInnerHTML={{__html: heading}}
        />
        <dl className={css.list}>
          {items}
        </dl>
      </Container>
    </section>
  )
}

export default memo(Schedule)