import { Fragment, memo, useContext, useMemo } from 'react'
import css from './Stage2.module.scss'
import Container from 'components/templates/Container/Container'
import { Stage2Data } from 'Pages/HomePage/Stage2/_assets/data'
import Heading from 'components/atoms/Heading/Heading'
import { HeadingTypes } from 'components/atoms/Heading/Heading.spec'
import image1 from 'assets/images/step2_1.jpg'
import image2 from 'assets/images/step2_2.jpg'
import { LangContext } from 'components/organisms/LangProvider/LangProvider'

function Stage2 () {
  const lang = useContext(LangContext)

  const {
    heading,
    list
  } = Stage2Data[lang]

  const content = useMemo(() => {
    return list.map(({heading, description, sublist}, index) => (
      <Fragment key={index}>
        <Heading
          headingType={HeadingTypes.H3}
          headingStyle={HeadingTypes.H4}
          className={css.subheading}
        >
          {heading}
        </Heading>
        {description && (
          <p className={css.description} dangerouslySetInnerHTML={{__html: description}} />
        )}
        {sublist && (
          <ul className={css.list}>
            {sublist.map((item, index) => (
              <li
                className={css.item}
                dangerouslySetInnerHTML={{__html: item}}
                key={index}
              />
            ))}
          </ul>
        )}
      </Fragment>
    ))
  }, [list])

  return (
    <section>
      <Container className={css.wrapper}>
        <Heading
          dangerouslySetInnerHTML={{__html: heading}}
        />
        {content}
        <img
          src={image1}
          className={css.image1}
          alt="Factory facilities#1"
        />
        <img
          src={image2}
          className={css.image2}
          alt="Factory facilities#2"
        />
      </Container>
    </section>
  )
}

export default memo(Stage2)