import { memo, useCallback, useContext, useMemo } from 'react'
import css from 'Pages/HomePage/ContactUs/ContactUs.module.scss'
import Container from 'components/templates/Container/Container'
import { ContactsData } from 'Pages/HomePage/ContactUs/_assets/data'
import Heading from 'components/atoms/Heading/Heading'
import { useForm } from 'react-hook-form'
import classnames from 'classnames'
import { LangContext } from 'components/organisms/LangProvider/LangProvider'

function ContactUs () {
  const lang = useContext(LangContext)

  const {
    heading,
    form,
    buttonLabel
  } = ContactsData[lang]

  const {register, handleSubmit} = useForm()

  const formItems = useMemo(() => {
    return form.map(({name, className, ...item}, index) => (
      <input
        className={classnames(css.input, className)}
        key={index}
        {...register(name)}
        {...item}
      />
    ))

  }, [form, register])

  const submitForm = useCallback(data => {
    console.log(data)
    alert(`Submit data`)
    // reset()
  }, [])

  return (
    <section className={css.wrapper}>
      <Container>
        <Heading className={css.heading}>
          {heading}
        </Heading>
        <form onSubmit={handleSubmit(submitForm)}>
          <div className={css.form}>
            {formItems}
          </div>
          <button
            className={css.button}
            type="submit"
          >
            {buttonLabel}
          </button>
        </form>
      </Container>
    </section>
  )
}

export default memo(ContactUs)