import { memo, useCallback, useContext, useMemo } from 'react'
import css from './Application.module.scss'
import { PropsWithLang } from 'specs/index.spec'
import Container from 'components/templates/Container/Container'
import Heading from 'components/atoms/Heading/Heading'
import { ApplicationData } from 'Pages/HomePage/Application/_assets/data'
import { HeadingTypes } from 'components/atoms/Heading/Heading.spec'
import { useForm } from 'react-hook-form'
import { LangContext } from 'components/organisms/LangProvider/LangProvider'

function Application () {
  const lang = useContext(LangContext)

  const {
    heading,
    form,
    buttonLabel
  } = ApplicationData[lang]

  const {register, handleSubmit} = useForm()

  const inputs = useMemo(() => {
    return form.map(({name, ...item}, index) => (
      <input
        className={css.input}
        key={index}
        {...register(name)}
        {...item}
      />
    ))
  }, [register, form])

  const submitForm = useCallback(data => {
    console.log(data)
    alert(`Submit application data`)
  }, [])

  return (
    <section className={css.wrapper}>
      <Container className={css.container}>
        <Heading
          headingStyle={HeadingTypes.H3}
          className={css.heading}
        >
          {heading}
        </Heading>
        <form onSubmit={handleSubmit(submitForm)}>
          <div className={css.form}>
            {inputs}
          </div>
          <button
            className={css.button}
            type="submit"
          >
            {buttonLabel}
          </button>
        </form>
      </Container>
    </section>
  )
}

export default memo(Application)