import { memo, useContext } from 'react'
import css from './Design.module.scss'
import Container from 'components/templates/Container/Container'
import { DesignData } from 'Pages/HomePage/Design/_assets/data'
import Heading from 'components/atoms/Heading/Heading'
import image1 from 'assets/images/design_1.jpg'
import image2 from 'assets/images/design_2.jpg'
import image3 from 'assets/images/design_3.jpg'
import { LangContext } from 'components/organisms/LangProvider/LangProvider'

function Design () {
  const lang = useContext(LangContext)

  const {
    heading,
    description
  } = DesignData[lang]

  return (
    <section>
      <Container className={css.container}>
        <Heading
          dangerouslySetInnerHTML={{__html: heading}}
          className={css.heading}
        />
        <p
          className={css.description}
          dangerouslySetInnerHTML={{__html: description}}
        />
      </Container>
      <ul className={css.slides}>
        <li className={css.item}>
          <img
            src={image1}
            className={css.image}
            alt="Design example#1"
          />
        </li>
        <li className={css.item}>
          <img
            src={image2}
            className={css.image}
            alt="Design example#2"
          />
        </li>
        <li className={css.item}>
          <img
            src={image3}
            className={css.image}
            alt="Design example#3"
          />
        </li>
      </ul>
    </section>
  )
}

export default memo(Design)