import { memo, useContext, useMemo } from 'react'
import css from './Founders.module.scss'
import Container from 'components/templates/Container/Container'
import { FoundersData } from 'Pages/HomePage/Founders/_assets/data'
import Heading from 'components/atoms/Heading/Heading'
import { HeadingTypes } from 'components/atoms/Heading/Heading.spec'
import { LangContext } from 'components/organisms/LangProvider/LangProvider'

function Founders () {
  const lang = useContext(LangContext)

  // useEffect(() => {
  //   navigator.clipboard.writeText(`Hello world!`)
  //     .then(() => {
  //       alert(`Text added to clipboard!`)
  //     })
  //     .catch(error => {
  //       alert(error)
  //     })
  // }, [])

  const {
    heading,
    list
  } = FoundersData[lang]

  const items = useMemo(() => {
    return list.map(({name, rank, photo} : {name: string, rank: string, photo: string}, index: number) => (
      <li className={css.item} key={index}>
        <button
          onClick={() => alert(`Modal for ${name} data would be here`)}
          className={css.button}
          type="button"
        >
          <figure className={css.content}>
            <img
              src={photo}
              className={css.photo}
              alt={name}
            />
            <figcaption>
              <Heading
                headingType={HeadingTypes.H3}
                headingStyle={HeadingTypes.H4}
                className={css.name}
              >
                {name}
              </Heading>
              <span
                className={css.rank}
                dangerouslySetInnerHTML={{__html: rank}}
              />
            </figcaption>
          </figure>
        </button>
      </li>
    ))
  }, [list])

  return (
    <section className={css.wrapper}>
      <Container>
        <Heading>
          {heading}
        </Heading>
        <ul className={css.list}>
          {items}
        </ul>
      </Container>
    </section>
  )
}

export default memo(Founders)