import { memo } from 'react'
import css from './City.module.scss'
import classnames from 'classnames'
import { PropsWithClassName } from 'specs/index.spec'

export enum CityNamePosition {
  LEFT = `LEFT`,
  TOP = `TOP`
}

function City ({
  name,
  position,
  className
}: {
  name: string,
  position?: string,
} & PropsWithClassName) {
  return (
    <div
      className={classnames(css.point, className, {
        [css.pointLeft]: position === CityNamePosition.LEFT
      })}
    >
      <span
        className={css.name}
      >
        {name}
      </span>
    </div>
  )
}

export default memo(City)