import { createContext, memo } from 'react'
import { useSelector } from 'react-redux'
import { RootReducerType } from 'store/spec/index.spec'
import { LanguageOptions } from 'utils/const'

export const LangContext = createContext(LanguageOptions.UA)

function LangProvider ({children}: {children: JSX.Element}) {
  const lang = useSelector((store: RootReducerType) => store.ui.lang)

  return (
    <LangContext.Provider value={lang}>
      {children}
    </LangContext.Provider>
  )
}

export default memo(LangProvider)