import { memo, useContext } from 'react'
import css from './AboutFund.module.scss'
import Container from 'components/templates/Container/Container'
import Heading from 'components/atoms/Heading/Heading'
import { HeadingTypes } from 'components/atoms/Heading/Heading.spec'
import { AboutFundData } from 'Pages/HomePage/AboutFund/_assets/data'
import Locations from 'components/molecules/Locations/Locations'
import { LangContext } from 'components/organisms/LangProvider/LangProvider'

function AboutFund () {
  const lang = useContext(LangContext)

  const {
    heading,
    description,
    label
  } = AboutFundData[lang]

  return (
    <section className={css.wrapper}>
      <Container className={css.container}>
        <div className={css.frame}>
          <Heading
            headingType={HeadingTypes.H1}
            headingStyle={HeadingTypes.H1}
            dangerouslySetInnerHTML={{__html: heading}}
          />
          <p
            className={css.description}
            dangerouslySetInnerHTML={{__html: description}}
          />
          <span
            className={css.label}
            dangerouslySetInnerHTML={{__html: label}}
          />
        </div>
      </Container>
      <Locations
        className={css.map}
        lang={lang}
      />
    </section>
  )
}

export default memo(AboutFund)