import { memo, useContext, useMemo } from 'react'
import css from './Contacts.module.scss'
import Container from 'components/templates/Container/Container'
import { ContactsData } from 'Pages/HomePage/Contacts/_assets/data'
import Heading from 'components/atoms/Heading/Heading'
import { HeadingTypes } from 'components/atoms/Heading/Heading.spec'
import classnames from 'classnames'
import IconDownload from 'assets/icons/IconDownload'
import { LangContext } from 'components/organisms/LangProvider/LangProvider'

function Contacts () {
  const lang = useContext(LangContext)

  const {
    heading,
    list,
    download
  } = ContactsData[lang]

  const items = useMemo(() => {
    return list.map(({name, phone, email}, index) => (
      <div className={css.item} key={index}>
        <dt className={css.name}>
          {name}
        </dt>
        <dd className={css.contacts}>
          <a
            className={css.link}
            href={`tel:${phone.split(`-`).join(``)}`}
          >
            {phone}
          </a>
          <a
            className={classnames(css.link, css.email)}
            href={`mailto:${email}`}
          >
            {email}
          </a>
        </dd>
      </div>
    ))
  }, [list])

  return (
    <section className={css.wrapper}>
      <Container className={css.container}>
        <address className={css.address}>
          <Heading
            className={css.heading}
            headingStyle={HeadingTypes.H3}
          >
            {heading}
          </Heading>
          <dl className={css.list}>
            {items}
          </dl>
        </address>
        <a
          href={download.url}
          className={css.download}
          target="_blank"
          rel="noreferrer noopener"
        >
          <IconDownload className={css.icon} />
          {download.label}
        </a>
      </Container>
    </section>
  )
}

export default memo(Contacts)