import { memo } from 'react'
import css from './Footer.module.scss'
import Container from 'components/templates/Container/Container'
import IconLogoWhite from 'assets/icons/IconLogoWhite'
import Navigation from 'components/molecules/Navigation/Navigation'
import ButtonLang from 'components/organisms/ButtonLang/ButtonLang'
import Social from 'components/molecules/Social/Social'

function Footer () {
  return (
    <footer className={css.wrapper}>
      <Container className={css.container}>
        <IconLogoWhite className={css.logo} />
        <Navigation
          className={css.navigation}
          isLight
        />
        <ButtonLang
          className={css.lang}
          isLight
        />
        <Social
          className={css.social}
          isDark
        />
      </Container>
    </footer>
  )
}

export default memo(Footer)