import { memo } from 'react'
import css from 'components/organisms/Header/Header.module.scss'
import Container from 'components/templates/Container/Container'
import { HeaderProps } from 'components/organisms/Header/Header.spec'
import IconLogoColor from 'assets/icons/IconLogoColor'
import Navigation from 'components/molecules/Navigation/Navigation'
import ButtonLang from 'components/organisms/ButtonLang/ButtonLang'
import ButtonSupport from 'components/organisms/ButtonSupport/ButtonSupport'
import Social from 'components/molecules/Social/Social'

function Header ({className, headerRef}: HeaderProps) {
  return (
    <header className={className} ref={headerRef}>
      <Container className={css.container}>
        <IconLogoColor className={css.logo} />
        <nav className={css.nav}>
          <Navigation />
        </nav>
        <ButtonLang className={css.lang} />
        <Social className={css.social} />
        <ButtonSupport className={css.support} />
      </Container>
    </header>
  )
}

export default memo(Header)