import { memo, useContext } from 'react'
import css from './Stage1.module.scss'
import Container from 'components/templates/Container/Container'
import { Stage1Data } from 'Pages/HomePage/Stage1/_assets/data'
import image1 from 'assets/images/step1_1.jpg'
import image2 from 'assets/images/step1_2.jpg'
import Heading from 'components/atoms/Heading/Heading'
import { LangContext } from 'components/organisms/LangProvider/LangProvider'

function Stage1 () {
  const lang = useContext(LangContext)

  const {
    heading,
    description,
    label
  } = Stage1Data[lang]

  return (
    <section className={css.wrapper}>
      <Container className={css.container}>
        <Heading
          dangerouslySetInnerHTML={{__html: heading}}
        />
        <p
          className={css.description}
          dangerouslySetInnerHTML={{__html: description}}
        />
      </Container>
      <img
        src={image1}
        className={css.image1}
        alt="House example#1"
      />
      <div className={css.frame}>
        <img
          src={image2}
          className={css.image2}
          alt="House example#1"
        />
        <span className={css.label}>
          {`${label} `}
          <a
            className={css.link}
            href="http://www.balbec.com"
            target="_blank"
            rel="noreferrer noopener"
          >
            balbec.com
          </a>
        </span>
      </div>
    </section>
  )
}

export default memo(Stage1)